export function getListComponentOfTemplate(
  list: any[],
  listCurrent: any[] = [],
) {
  const listForm: any[] = listCurrent;
  list.forEach(item => {
    listForm.push(item);
    if (item?.components) {
      getListComponentOfTemplate([...item.components], listForm);
    }
  });

  return listForm;
}

