export const convertToISO = (input: any, format: any) => {
  if (typeof input !== 'string' || typeof format !== 'string') {
    // console.error('Invalid input or format');
    return null;
  }

  const hasTime = format.includes('HH');
  let datePart = input;
  let timePart = '00:00:00';

  if (hasTime) {
    [datePart, timePart] = input.split(' ');
  }

  const dateSeparators = /[\/\-\.]/;
  const dateParts = datePart.split(dateSeparators).map(Number);
  const [hours, minutes, seconds] = (timePart || '00:00:00')
    .split(':')
    .map(Number);

  let year, month, day;

  switch (format) {
    case 'DD/MM/YYYY':
    case 'DD/MM/YYYY HH:mm:ss':
      [day, month, year] = dateParts;
      break;
    case 'MM/DD/YYYY':
    case 'MM/DD/YYYY HH:mm:ss':
      [month, day, year] = dateParts;
      break;
    case 'YYYY/MM/DD':
      [year, month, day] = dateParts;
      break;
    case 'YYYY/DD/MM HH:mm:ss':
      [year, day, month] = dateParts;
      break;
    default:
      console.error('Unsupported format');
      return null;
  }

  if ([day, month, year, hours, minutes, seconds].some(isNaN)) {
    // console.error('Invalid date or time values');
    return null;
  }

  const date = new Date(
    Date.UTC(year, month - 1, day, hours, minutes, seconds),
  );
  return date.toISOString();
};

export const findLegalFromListOrganization = (
  list: IOrganization[],
  orgIn?: string,
): IOrganization | undefined => {
  const orgInList = list?.find(o => o.type === 'LEGAL' && o.orgIn === orgIn);
  if (orgInList) {
    return orgInList;
  } else {
    const listSplitOrg = orgIn?.split('/');
    if (listSplitOrg && listSplitOrg?.length <= 3) {
      const orgInListParent = list?.find(o => o.orgIn === orgIn);
      return orgInListParent;
    }
    const parentOrg = listSplitOrg?.slice(0, -1)?.join('/');
    return findLegalFromListOrganization(list, parentOrg);
  }
};

