import {StatusBar, TouchableOpacity, View} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import {IconEmpty} from '../../../../../assets/images';
import styles from './styles';
import {SafeAreaView} from 'react-native-safe-area-context';
import TextCM from '../../../../components/Text';
import ButtonCM from '../../../../components/Button';
import {useNavigation, useRoute} from '@react-navigation/native';
import {Color, NavigationName} from '../../../../../constants';
import {SizeDP} from '../../../../../constants/Size';
import {useTranslation} from 'react-i18next';
import {Status} from '../../../../../model/enum/common.enum';
import {useAppSelector} from '../../../../../hooks/useRedux';
import React from 'react';
import {TabName} from '../../../../../navigation/BottomTabERequest/helper';
import {LIST_APP_TYPE} from '../CreateTicket/helper';

const CreateTicketSuccessScreen = () => {
  const router = useRoute<any>();
  const navigation = useNavigation<any>();
  const actionTicketFrom =
    (router?.params?.actionTicketFrom as LIST_APP_TYPE) || 'eRequest';
  const ticketId = router?.params?.ticketId || '';
  const {dataAccount} = useAppSelector(state => state.account);
  const {t} = useTranslation();

  const backHomeScreen = () => {
    if (actionTicketFrom === 'eBLDT') {
      navigation.navigate('MyReceiptScreen', {
        isReload: true,
      });
    } else {
      navigation.navigate(TabName.Home);
    }
  };

  const navigateToTicketDetails = () => {
    if (ticketId) {
      navigation.navigate(NavigationName.BottomTabNavigationTicketDetail, {
        dataTicketId: ticketId,
        email: dataAccount.email,
        status: Status.processing,
        selectTabBottom: 1,
      });
    }
  };

  const checkShowViewDetail = () => {
    if (actionTicketFrom === 'eBLDT') {
      return false;
    }
    return true;
  };

  return (
    <>
      <StatusBar
        translucent
        barStyle={'dark-content'}
        backgroundColor={Color.Transparent}
      />
      <HeaderCM
        style={styles.ctnHeader}
        contentStyle={styles.ctnHeader}
        titleStyle={{
          color: Color.TextPrimary,
        }}
        title={t('HM.addSearch')}
        onPressIconLeft={backHomeScreen}
      />
      <SafeAreaView edges={['bottom', 'left', 'right']} style={styles.ctnArea}>
        <View style={{flex: 1, alignItems: 'center', justifyContent: 'center'}}>
          <IconEmpty width={SizeDP(331)} height={SizeDP(217)} />
          <TextCM style={styles.textTitle}>{t('HM.createSuccess')}</TextCM>
          {/* <TextCM style={styles.textDes}>Mã giao dịch của bạn là #456345</TextCM> */}
          {checkShowViewDetail() && (
            <TouchableOpacity onPress={navigateToTicketDetails}>
              <TextCM style={styles.textDetail}>{t('CM.viewDetail')}</TextCM>
            </TouchableOpacity>
          )}
        </View>
        <ButtonCM
          style={{width: '60%', marginTop: SizeDP(16)}}
          label={t('HM.home')}
          onPress={backHomeScreen}
        />
      </SafeAreaView>
    </>
  );
};

export default CreateTicketSuccessScreen;
