import {
  Alert,
  FlatList,
  Image,
  Keyboard,
  Platform,
  StatusBar,
  TouchableOpacity,
  TouchableWithoutFeedback,
  useWindowDimensions,
  View,
} from 'react-native';
import HeaderCM from '../../../../components/Header/HeaderCM';
import styles from './styles';
import {
  IconArrowGrey,
  IconEmpty,
  IconMail,
  IconPhone,
  IconTree,
} from '../../../../../assets/images';
import {SizeDP} from '../../../../../constants/Size';
import WebView, {WebViewMessageEvent} from 'react-native-webview';
import {useCallback, useEffect, useRef, useState} from 'react';
import {SafeAreaView} from 'react-native-safe-area-context';
import {Color, NavigationName, String} from '../../../../../constants';
import DropdownCM, {IDataDropdown} from '../../../../components/DropDown';
import BottomSheetCM from '../../../../components/BottomSheet';
import {
  useFocusEffect,
  useNavigation,
  useRoute,
} from '@react-navigation/native';
import {IModeler} from '../../../../../model/interface/modeler.interface';
import {isEmpty} from 'lodash';
import {useAppSelector} from '../../../../../hooks/useRedux';
import TextCM from '../../../../components/Text';
import ModalConfirmCM from '../../../../components/ModalConfirm';
import TextFieldCM from '../../../../components/TextField';
import LoadingCM from '../../../../components/Loading';
import {ITicket} from '../../../../../model/interface/service.interface';
import CardTicketRelative from '../CardTicketRelative';
import {useTranslation} from 'react-i18next';
import {Divider, Icon} from '@ui-kitten/components';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {
  IAclProcess,
  IAclTicket,
  IImplementer,
  IPerson,
  IVariable,
} from './interface';
import {useReviewServices} from '../../../../../hooks/apiHooks/useReviewTicketServices';
import React from 'react';
import {getConfig} from '../../../../../services/httpClient';
import Toast from 'react-native-toast-message';
import ModalFullScreen from '../../../../components/ModalFullScreen';
import ButtonCM from '../../../../components/Button';
import {TabName} from '../../../../../navigation/BottomTabERequest/helper';
import ListServicesRelative from '../ListServiceRelative';
import {
  convertToISO,
  dataProrityDummy,
  dataTiket,
  FIELD_FORM_TYPE_INHERIT,
  findLegalFromListOrganization,
  getListComponentOfTemplate,
  LIST_APP_TYPE,
} from './helper';
import ScanScreen from '../../../../components/Scan';
import {IOrganization} from '../../../review-ecollabo-flow/components/share/interface';
import {useOriginServices} from '../../../../../hooks/apiHooks/useOriginServices';

const CreateTicketScreen = () => {
  const {t} = useTranslation();
  const route = useRoute<any>();
  const actionTicketFrom =
    (route?.params?.actionTicketFrom as LIST_APP_TYPE) || 'eRequest';
  const webviewRef = useRef<WebView>(null);
  const navigation = useNavigation<any>();
  const action = route?.params?.action ?? 'create';

  const [dataPrority, setDataPrority] = useState<IDataDropdown>(
    dataProrityDummy[2],
  );
  const [dataModeler, setDataModeler] = useState<IModeler>();
  const [listAclProcess, setListAclProcess] = useState<IAclProcess[]>([]);
  const [listImplement, setListImplement] = useState<IImplementer[]>([]);
  const [listPersonRelateProcess, setListPersonRelateProcess] = useState<
    IPerson[]
  >([]);
  const [ticketStatusSelect, setTicketStatusSelect] = useState<{
    status: 'processing' | 'draft';
    isSaveDraftButSubmit?: boolean;
  }>({
    status: 'processing',
    isSaveDraftButSubmit: false,
  });
  const [variableData, setVariableData] = useState<IVariable[]>([]);
  const [formVersion, setFormVersion] = useState<string>();
  const [ticketName, setTicketName] = useState<string>('');
  const [errorMessage, setErrorMessage] = useState<string>('');
  const [isVisible, setIsVisible] = useState<boolean>(false);
  const [isVisibleTicketRelative, setIsVisibleTicketRelative] =
    useState<boolean>(false);
  const [isVisibleCancel, setIsVisibleCancel] = useState<boolean>(false);
  const [isVisibleNoti, setIsVisibleNoti] = useState<boolean>(false);
  const [isVisibleInfo, setIsVisibleInfo] = useState<boolean>(false);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [dataTicket, setDataTicket] = useState<any>();
  const [fieldDataTicketNameType, setFieldDataTicketNameType] = useState('');
  const [isSabecoOIA, setIsSabecoOIA] = useState<boolean>(false);
  const [codeChannel, setCodeChannel] = useState<string | null>(null);
  const [showModalListModelerId, setShowModalListModelerId] = useState(false);
  const [listTicketRelativeSelect, setListTicketRelativeSelect] = useState<
    ITicket[]
  >([]);
  const [isDisableTicketName, setIsDisableTicketName] =
    useState<boolean>(false);
  const [heightOfKeyboard, setHeightOfKeyboard] = useState(0);
  const [isShowModalScan, setIsShowModalScan] = useState(false);
  const [selectTypeOnMessage, setSelectTypeOnMessage] = useState<
    'init' | 'saveDraft' | 'submit' | null
  >(null);
  const [showAction, setShowAction] = useState<boolean>(false);
  const ListServices = useTicketServices();
  const ReviewService = useReviewServices();
  const OrgInService = useOriginServices();

  const {width} = useWindowDimensions();
  const dataAccount = useAppSelector(state => state.account.dataAccount);
  const token = useAppSelector(state => state.common.accessToken);

  useFocusEffect(
    useCallback(() => {
      if (route?.params?.codeChannel) {
        setCodeChannel(route?.params?.codeChannel);
      }
      if (route?.params?.modelerId) {
        setShowModalListModelerId(true);
      } else {
        if (route?.params?.paramsSelectTicket?.length >= 0) {
          setIsVisibleTicketRelative(true);
        }
      }
    }, [route?.params]),
  );

  useEffect(() => {
    const sub = Keyboard.addListener(
      Platform.OS === 'android' ? 'keyboardDidShow' : 'keyboardWillShow',
      e => {
        setHeightOfKeyboard(e.endCoordinates.height);
      },
    );
    const sub1 = Keyboard.addListener(
      Platform.OS === 'android' ? 'keyboardDidHide' : 'keyboardWillHide',
      e => {
        setHeightOfKeyboard(0);
      },
    );
    return () => {
      sub.remove();
      sub1.remove();
    };
  }, []);

  useEffect(() => {
    (async () => {
      try {
        setTicketName('');
        if (route?.params?.dataTicket?.modelerId) {
          await getDataModerler();
        }
        if (route?.params?.dataTicket?.id && action !== 'create') {
          const data = await getTicketById();
          if (data) {
            setDataTicket(data);
            setTicketName(data?.title);
            const priority = dataProrityDummy.find(
              p => p?.value === data.priority,
            );
            if (priority) {
              setDataPrority(priority);
            }
            const dataForm = data.formData?.find(f => !f.userTaskId);
            if (dataForm && dataForm.formData) {
              setFormVersion(dataForm.version);
            }

            // get ticket relative
            const dataTicketRelative = data.listRelativeTicket;
            if (dataTicketRelative.length > 0) {
              navigation.setParams({
                ...route?.params,
                paramsSelectTicket: dataTicketRelative,
              });
            }
          }
        }
      } catch (error) {}
    })();
  }, []);

  const getDataModerler = async () => {
    try {
      const values = await Promise.all([
        ListServices.getModeler(route?.params?.dataTicket?.modelerId, token),
        ListServices.getImplementByModelerId(
          route?.params?.dataTicket?.modelerId,
        ),
        ListServices.getAclProcessByModelerId(
          route?.params?.dataTicket?.modelerId,
        ),
      ]);
      const dataModeler = values[0] as IModeler;
      setDataModeler(dataModeler);
      setFormVersion(dataModeler.formVersion);
      const dataForm = await ListServices.getFormByVersion(
        dataModeler.formVersion,
      );
      if (dataForm) {
        let jsonForm = dataForm.jsonForm;
        if (typeof jsonForm !== 'string') {
          jsonForm = JSON.stringify(jsonForm);
        }
        // #18032025# là code duy nhất cho hợp đồng OIA của sabeco
        if (jsonForm.includes('#18032025#')) {
          setIsSabecoOIA(true);
          setTicketName('OIA_Kênh_Tên điểm bán');
        }
        checkTicketName(jsonForm);
      }
      const listAclProcess = values[2].data.content as IAclProcess[];
      const listImplement = values[1].data as IImplementer[];
      setListImplement(listImplement);
      setListAclProcess(listAclProcess);

      const listFollowId = dataModeler.followerId
        ? dataModeler.followerId.split(',').map(Number)
        : [];
      const listManagerId = dataModeler.managerId
        ? dataModeler.managerId.split(',').map(Number)
        : [];
      const listUserId = Array.from(
        new Set(
          listFollowId
            .concat(listManagerId)
            .filter(
              u => !listImplement.some(i => +(i as any)?.executorId === u),
            ),
        ),
      );
      const listApi = [];
      if (dataModeler.documentId) {
        listApi.push(
          ListServices.getListDataVariableConfig(dataModeler.documentId),
        );
      }
      if (listUserId.length > 0) {
        listApi.push(
          ReviewService.findUserByOrgIn(0, 10000, '', null, listUserId),
        );
      }
      const valuesUser = await Promise.all(listApi);
      let variableData;
      let listPersonRelateProcess: IPerson[] = [];
      if (dataModeler.documentId && !listUserId.length) {
        variableData = valuesUser[0].variables.filter(
          (v: any) => v.apply && v.resourceId === dataModeler.formId,
        );
      } else if (!dataModeler.documentId && listUserId.length) {
        listPersonRelateProcess = listUserId.map(userId => {
          const per = (valuesUser[0].data as IPerson[]).find(
            u => u.resourceId === userId,
          );
          return per;
        }) as IPerson[];
      } else {
        variableData = valuesUser[0].variables.filter(
          (v: any) => v.apply && v.resourceId === dataModeler.formId,
        );
        listPersonRelateProcess = listUserId.map(userId => {
          const per = (valuesUser[1].data as IPerson[]).find(
            u => u.resourceId === userId,
          );
          return per;
        }) as IPerson[];
      }
      setVariableData(variableData);
      setListPersonRelateProcess(listPersonRelateProcess);
    } catch (error) {}
  };

  const getOrganizationsSupervisingByCode = async (orgIn: string) => {
    try {
      const result = await ReviewService.findUserByOrgIn(0, 10, '', orgIn);
      return result.data;
    } catch (error) {
      // xử lý lỗi nếu cần
      console.error(error);
      throw error;
    }
  };

  const getListOrgByOrgIn = async (type: 'my' | 'superior' | 'all') => {
    let superiorOrganization;
    let myOrganization;
    const sourceOrgIn = dataAccount?.orgIn?.split('/').slice(0, 3).join('/');
    const params = {
      orgIn: sourceOrgIn,
    };
    const dataResp = await ReviewService.getDataOrgByOrgin(params);
    myOrganization = findLegalFromListOrganization(
      dataResp,
      dataAccount?.orgIn,
    );
    if (type === 'my') {
      return {
        myOrganizationName: myOrganization?.name,
        superiorOrganizationName: '',
        myOrganization: myOrganization,
      };
    }
    const splitOrgIn =
      type === 'all'
        ? myOrganization?.orgIn?.split('/')?.slice(0, -1)
        : dataAccount?.orgIn?.split('/')?.slice(0, -1);
    if (splitOrgIn && splitOrgIn?.length <= 3) {
      superiorOrganization = dataResp?.find(
        (o: IOrganization) => o.orgIn === splitOrgIn?.join('/'),
      );
    } else {
      superiorOrganization = findLegalFromListOrganization(
        dataResp,
        splitOrgIn?.join('/'),
      );
    }

    return {
      myOrganizationName: myOrganization?.name,
      superiorOrganizationName: superiorOrganization?.name,
    };
  };

  const getInfoOrganization = async (orgId: string) => {
    const dataResp = await OrgInService.getOrigin(orgId);
    return dataResp;
  };

  const checkFieldName = async (jsonForm: string) => {
    if (jsonForm && jsonForm.length) {
      if (
        jsonForm.includes('#accountname#') ||
        jsonForm.includes('#emailCreateBy#') ||
        jsonForm.includes('#organizationCreateBy#') ||
        jsonForm.includes('#organizationSupervising#') ||
        jsonForm.includes('#keyValueModeler#') ||
        jsonForm.includes('#myOrganization#') ||
        jsonForm.includes('#superiorOrganization#') ||
        jsonForm.includes('#addressSubsidiaries#') ||
        jsonForm.includes('#taxCodeSubsidiaries#')
      ) {
        let organizationCreateBy = null;
        let organizationSupervising = null;
        let myOrganization = null;
        let superiorOrganization = null;
        let addressSubsidiaries = null;
        let taxCodeSubsidiaries = null;
        if (jsonForm.includes('#organizationCreateBy#')) {
          const result = await getOrganizationsSupervisingByCode(
            dataAccount?.orgIn || '',
          );
          if (result) {
            organizationCreateBy = result[0]?.orgName;
          }
        }
        if (jsonForm.includes('#organizationSupervising#')) {
          let data = dataAccount?.orgIn?.split('/');
          const result_ =
            data && data?.length > 3
              ? data?.slice(0, -1).join('/')
              : dataAccount?.orgIn;
          if (result_) {
            const result = await getOrganizationsSupervisingByCode(result_);
            if (result) {
              organizationSupervising = result[0]?.orgName;
            }
          }
        }

        if (
          jsonForm.includes('#myOrganization#') ||
          jsonForm.includes('#superiorOrganization#')
        ) {
          const checkType =
            jsonForm.includes('#myOrganization#') &&
            jsonForm.includes('#superiorOrganization#')
              ? 'all'
              : jsonForm.includes('#myOrganization#') &&
                !jsonForm.includes('#superiorOrganization#')
              ? 'my'
              : 'superior';
          const result = await getListOrgByOrgIn(checkType);
          if (result) {
            myOrganization = result?.myOrganizationName;
            superiorOrganization = result?.superiorOrganizationName;
          }
        }

        if (
          jsonForm.includes('#addressSubsidiaries#') ||
          jsonForm.includes('#taxCodeSubsidiaries#')
        ) {
          const {myOrganization} = await getListOrgByOrgIn('my');
          if (myOrganization) {
            addressSubsidiaries = myOrganization?.address;
            taxCodeSubsidiaries = myOrganization?.taxCode;
          }
        }

        let arrJsonForm = JSON.parse(jsonForm);
        arrJsonForm = getListComponentOfTemplate(arrJsonForm || []).filter(
          item => FIELD_FORM_TYPE_INHERIT.includes(item.type),
        );
        arrJsonForm = arrJsonForm.map((d: any) => {
          if (d.type == 'datepicker' && d.value) {
            d.value = convertToISO(d.value, d.validate.formatDate);
          }
          // if (d.type == 'table_advance') {
          //   d.value = d?.tableAdvanceConfig?.dataCell_value;
          // }

          if (d.code == '#organizationCreateBy#') {
            return {...d, value: organizationCreateBy};
          }

          if (d.code == '#organizationSupervising#') {
            return {...d, value: organizationSupervising};
          }
          if (d.code == '#myOrganization#') {
            return {...d, value: myOrganization};
          }
          if (d.code == '#superiorOrganization#') {
            return {...d, value: superiorOrganization};
          }

          if (d.code == '#addressSubsidiaries#') {
            return {...d, value: addressSubsidiaries};
          }

          if (d.code == '#taxCodeSubsidiaries#') {
            return {...d, value: taxCodeSubsidiaries};
          }

          if (d.code == '#accountname#') {
            return {
              ...d,
              value: '' + dataAccount.firstName + ' ' + dataAccount.lastName,
            };
          }

          if (d.code == '#emailCreateBy#') {
            return {...d, value: '' + dataAccount.email};
          }

          if (d.code == '#keyValueModeler#') {
            return {...d, value: dataModeler?.keyValue};
          }
          return {...d};
        });
        const listType = [
          '#accountname#',
          '#emailCreateBy#',
          '#organizationCreateBy#',
          '#organizationSupervising#',
          '#myOrganization#',
          '#superiorOrganization#',
          '#addressSubsidiaries#',
          '#taxCodeSubsidiaries#',
          '#keyValueModeler#',
        ];

        arrJsonForm = arrJsonForm
          ?.filter((d: any) => d.value)
          ?.filter((d: any) => listType?.includes(d.code));
        return arrJsonForm;
      }
    }
  };

  const checkTicketName = (jsonForm: string) => {
    if (jsonForm && jsonForm.length) {
      if (jsonForm.includes('"type": "textField_ticketName"')) {
        setIsDisableTicketName(true);
      }
    }
  };

  const onMessage = async (event: WebViewMessageEvent) => {
    const res = JSON.parse(event.nativeEvent.data);
    if (res?.result === 'e-from') {
      if (action !== 'create') {
        if (dataTicket) {
          const dataForm = dataTicket.formData?.find((f: any) => !f.userTaskId);
          if (dataForm && dataForm.formData) {
            // set value to form
            const formData = JSON.stringify({
              type: 'postFormEdit',
              data: dataForm.formData?.filter((d: any) => d.value),
            });

            const scriptJSX = `window.postMessage('${formData
              .replace(/\\/g, '\\\\')
              .replace(/'/g, "\\'")}', '*');`;
            setTimeout(() => {
              webviewRef?.current?.injectJavaScript(scriptJSX);
            }, 300);
          }
        }
      } else {
        setSelectTypeOnMessage('init');
        const data = JSON.stringify({
          result: 'saveDraft',
        });
        const scriptJSX = `window.postMessage('${data}', '*');`;
        webviewRef?.current?.injectJavaScript(scriptJSX);
      }
    }

    if (res?.result == 'result') {
      if (selectTypeOnMessage === 'init') {
        let dataForm = res.data;
        const dataFrom = await checkFieldName(JSON.stringify(dataForm));
        if (dataFrom) {
          const formData = JSON.stringify({
            type: 'postFormEdit',
            data: dataFrom,
          });
          const scriptJSX = `window.postMessage('${formData
            .replace(/\\/g, '\\\\')
            .replace(/'/g, "\\'")}', '*');`;
          setTimeout(() => {
            webviewRef?.current?.injectJavaScript(scriptJSX);
          }, 500);
        }
        return;
      }
      if (selectTypeOnMessage === 'saveDraft') {
        const dataForm = res.data;
        if (dataForm && isDisableTicketName) {
          let ticketName = dataForm.find(
            (d: any) => d.type == 'textField_ticketName',
          );
          if (ticketName) {
            setTicketName(ticketName.value);
          }
          if (isSabecoOIA) {
            const codeChannel = dataForm.find(
              (d: any) => d.code == '#codeChannel#',
            )?.value
              ? dataForm.find((d: any) => d.code == '#codeChannel#')?.value
              : null;
            setCodeChannel(codeChannel);
          }
        }
        if (fieldDataTicketNameType === 'confirm') {
          setIsVisibleInfo(true);
        } else {
          setIsVisibleTicketRelative(true);
        }
      } else if (selectTypeOnMessage === 'submit') {
        setIsLoading(true);
        const dataSelect = route?.params?.dataTicket;
        const listRelativeTicket = route?.params?.paramsSelectTicket
          ? route?.params?.paramsSelectTicket.map((item: ITicket) => {
              return {
                id: item.id,
              };
            })
          : [];

        if (dataSelect && dataModeler) {
          const aclTicketDTOS = getAclTicketDTOS(dataModeler);

          let dataBody = {
            aclTicketDTOS: aclTicketDTOS,
            followerId: dataModeler.followerId,
            formData: [
              {
                eformId: dataModeler.formId,
                formData: res?.data?.map((item: any) => {
                  return {
                    ...item,
                    dataType: convertTypeField(item),
                    typeForm: item.type,
                    value: item.value,
                  };
                }),
                version: formVersion,
              },
            ],
            jsonXml: dataModeler.jsonXml,
            listRelativeTicket: listRelativeTicket,
            priority: dataPrority.value,
            ticketStatus: ticketStatusSelect.status,
            title: ticketName,
            workFlowId:
              action !== 'create' ? dataSelect.workFlowId : dataSelect.id,
            modelerId: route?.params?.dataTicket?.modelerId,
          };

          if (dataTicket) {
            dataBody = {
              ...dataBody,
              formData: [
                {
                  ...dataTicket?.formData?.find((f: any) => !f.userTaskId),
                  eformId: dataModeler.formId,
                  formData: res?.data?.map((item: any) => {
                    return {
                      ...item,
                      dataType: convertTypeField(item),
                      typeForm: item.type,
                      value: item.value,
                    };
                  }),
                  version: formVersion,
                },
              ],
            };
          }
          try {
            if (action === 'edit') {
              const body = {
                ...dataTicket,
                ...dataBody,
                aclTicketDTOS: aclTicketDTOS,
                ticketStatus: ticketStatusSelect.status,
                title: ticketName,
                listRelativeTicket: listRelativeTicket,
                modelerId: route?.params?.dataTicket?.modelerId,
              };
              const dataTicketResp = await ListServices.updateTicket(body);
              if (dataTicketResp && ticketStatusSelect.status === 'draft') {
                if (actionTicketFrom === 'eBLDT') {
                  navigation.navigate('MyReceiptScreen', {
                    isReload: true,
                  });
                } else {
                  navigation.navigate(TabName.MyTicket);
                }
                Toast.show({
                  type: 'success',
                  text1: t('CM.noti'),
                  text2: t('NT.notification.creae_draft_success'),
                });
                return;
              }
              if (dataTicketResp) {
                navigation.navigate(NavigationName.CreateTicketSuccess, {
                  ticketId: dataTicketResp?.id,
                });
              }
            } else {
              const dataTicketResp = await ListServices.createTicket(
                dataBody,
                token,
              );
              if (dataTicketResp && ticketStatusSelect.status === 'draft') {
                navigation.navigate(TabName.MyTicket);
                Toast.show({
                  type: 'success',
                  text1: t('CM.noti'),
                  text2: t('NT.notification.creae_draft_success'),
                });
                return;
              }

              if (dataModeler?.documentId) {
                const dataJson = res?.data.filter((d: any) =>
                  variableData.some(v => v.fieldId == d.formId),
                );
                if (dataJson?.length > 0) {
                  const dataField = dataJson.map((data: any) => {
                    const find = variableData.find(
                      v => v.fieldId === data.formId,
                    );
                    return {
                      ...data,
                      code: find?.code,
                    };
                  });
                  const dataAdd = [
                    {
                      dataJson: dataField.reduce((tot: any, curr: any) => {
                        tot = {...tot, [curr.code]: curr.value};
                        return tot;
                      }, {}),
                    },
                  ];
                  try {
                    const dataSaveEwriter =
                      await ListServices.saveDataVariableEwriter(
                        dataAdd,
                        dataModeler.documentId,
                      );
                    if (dataSaveEwriter) {
                      navigation.navigate(NavigationName.CreateTicketSuccess, {
                        ticketId: dataTicketResp?.id,
                      });
                    } else {
                      Toast.show({
                        type: 'error',
                        text1: t('CM.noti'),
                        text2: t(
                          'NT.notification.save_info_into_ewriter_failed',
                        ),
                      });
                    }
                    return;
                  } catch (error) {}
                }
              }
              if (dataTicketResp) {
                navigation.navigate(NavigationName.CreateTicketSuccess, {
                  ticketId: dataTicketResp?.id,
                });
              }
            }
          } catch (error) {
            console.log(error);
          } finally {
            setTicketName('');
            setIsLoading(false);
          }
        }
      }
    }

    // open ocr element eform
    if (res?.type === 'eForm_OCR_ping') {
      setIsShowModalScan(true);
    }
  };

  const getAclTicketDTOS = (dataModeler: IModeler) => {
    let aclTicketDTOS = [];
    const listAclRelateTicket = listPersonRelateProcess.map(u => {
      const person = {
        id: u.resourceId,
        email: u.email,
      };
      const perm = ['v-d', 'v-i'];
      return {
        userTaskId: null,
        resourceName: ticketName,
        resourceType: 't',
        sharedType: 'p',
        personalStr: JSON.stringify(person),
        type: 'internal',
        perm: JSON.stringify(perm),
        custId: u.custId,
        orgIn: u.orgIn,
        createdBy: dataModeler.createdBy,
        typePerson: 'aclRelate',
      };
    });
    const listImplementAcl = listImplement.map(i => {
      const person = {
        id: +(i as any)?.executorId,
        email: i?.email,
      };
      const perm = ['v-d', 'v-i', 'e'];
      return {
        userTaskId: i.userTaskId,
        resourceName: ticketName,
        resourceType: 't',
        sharedType: i.executorId ? 'p' : null,
        personalStr: i.executorId ? JSON.stringify(person) : null,
        type: 'internal',
        perm: JSON.stringify(perm),
        custId: i.executorId ? i?.custId : null,
        orgIn: i.executorId ? i?.orgIn : null,
        createdBy: i.createdBy,
        typePerson: 'implement',
      };
    });
    const listSharedAcl: Partial<IAclTicket>[] = listAclProcess.map(acl => {
      return {
        userTaskId: acl.userTaskId,
        resourceName: ticketName,
        resourceType: 't',
        sharedType: acl.sharedType,
        personalStr: JSON.stringify(acl.personal),
        type: acl.type,
        perm: acl.perm,
        custId: acl.custId,
        orgIn: acl.orgIn,
        createdBy: acl.createdBy,
        typePerson: 'shareAcl',
      };
    });
    aclTicketDTOS = [
      ...listAclRelateTicket,
      ...listImplementAcl,
      ...listSharedAcl,
    ];
    return aclTicketDTOS;
  };

  const getTicketById = async () => {
    try {
      const data = (await ListServices.getTicketById(
        route?.params?.dataTicket?.id,
        token,
      )) as ITicket;
      return data;
    } catch (error) {
      console.log(error);
    }
  };

  const convertTypeField = (item: any) => {
    if (
      [
        'textField',
        'textArea',
        'password',
        'email',
        'phone_number',
        'radio_button',
        'upload_file',
        'upload_img',
        'hyperlink',
        'datepicker',
        'signature',
      ].includes(item.type)
    ) {
      return 'String';
    } else if (item.type === 'number') {
      return 'Number';
    }
    //  else if (item.type === 'datepicker') {
    //   return 'Date';
    // }
    else if (['check_box', 'dropdown'].includes(item.type)) {
      if (
        (item.type === 'dropdown' && item.validate.mutilChoose) ||
        item.type === 'check_box'
      ) {
        return 'List';
      }
      return 'String';
    }
    return null;
  };

  const handelSelectInfo = (item: IDataDropdown) => {
    if (item.value === 'information') {
      setIsVisible(true);
    } else if (item.value === 'relative') {
      setIsVisibleTicketRelative(true);
      fieldDataTicketName('relative');
    }
  };

  const handleCancel = (type: 'noti' | 'cancel' | 'info') => {
    switch (type) {
      case 'noti':
        setIsVisibleNoti(false);
        setIsVisibleInfo(true);
        break;
      case 'cancel':
        setIsVisibleCancel(false);
        break;
      case 'info':
        setIsVisibleInfo(false);
        !isSabecoOIA && setTicketName('');
        setErrorMessage('');
        break;
    }
  };

  const handleConfirmData = (
    type: 'processing' | 'draft',
    isDraftButSubmit = false,
  ) => {
    setTicketStatusSelect({
      status: type,
      isSaveDraftButSubmit: isDraftButSubmit,
    });
    if (
      isEmpty(ticketName) ||
      action === 'edit' ||
      action === 'multi' ||
      isSabecoOIA ||
      isDisableTicketName
    ) {
      if (isDisableTicketName) {
        // Toast.show({
        //   type: 'info',
        //   text1: t('CM.noti'),
        //   text2: t('Thông tin đang được xử lý, vui lòng đợi'),
        // });
        fieldDataTicketName('confirm');
      } else {
        setIsVisibleInfo(true);
      }
    } else {
      handleConfirmNoti(type, isDraftButSubmit);
    }
  };

  const handleConfirmCancel = () => {
    setIsVisibleCancel(false);
    navigation.goBack();
  };

  const fieldDataTicketName = (type: 'relative' | 'confirm') => {
    setFieldDataTicketNameType(type);
    setSelectTypeOnMessage('saveDraft');
    const data = JSON.stringify({
      result: 'saveDraft',
    });
    const scriptJSX = `window.postMessage('${data}', '*');`;
    webviewRef?.current?.injectJavaScript(scriptJSX);
  };

  const handleConfirmNoti = (
    type: 'processing' | 'draft',
    isDraftButSubmit = false,
  ) => {
    if (isDraftButSubmit) {
      setSelectTypeOnMessage('submit');
    } else {
      setSelectTypeOnMessage(type === 'processing' ? 'submit' : 'saveDraft');
    }
    const data = JSON.stringify({
      result: type === 'processing' ? 'submit' : 'saveDraft',
    });
    const scriptJSX = `window.postMessage('${data}', '*');`;
    webviewRef?.current?.injectJavaScript(scriptJSX);
    setIsVisibleNoti(false);
  };

  const handleConfirmInfo = () => {
    if (isEmpty(ticketName.trim())) {
      setErrorMessage(t('HM.notEmpty'));
      return;
    } else {
      handleConfirmNoti(
        ticketStatusSelect.status,
        ticketStatusSelect.isSaveDraftButSubmit,
      );
    }
    setIsVisibleInfo(false);
    setIsVisibleNoti(true);
    setErrorMessage('');
  };

  const handleAddTicket = () => {
    setIsVisibleTicketRelative(false);
    if (dataModeler?.inheritanceModeler?.modelerIds?.length) {
      setShowModalListModelerId(true);
    } else {
      navigation.navigate(NavigationName.AddTicketRelativeS, {
        paramsSelectTicket: route?.params?.paramsSelectTicket
          ? route?.params?.paramsSelectTicket
          : [],
        codeChannel: codeChannel,
        checkSabeco: isSabecoOIA,
        configFilter: dataModeler?.configFilter,
        inheritanceModeler: dataModeler?.inheritanceModeler,
        onGoBack: (paramsSelectTicket: ITicket[], codeChannel: any) => {
          navigation.setParams({paramsSelectTicket, codeChannel});
        },
      });
    }
  };

  const handleSetParamsConfig = async () => {
    navigation.setParams({paramsSelectTicket: listTicketRelativeSelect});
    setShowModalListModelerId(false);
    await handleGetValueForm();
  };

  const handleGetValueForm = async () => {
    const dataResp = await ListServices.getValueFormByTicketRelative({
      modelerId: dataModeler?.id,
      listRelativeTicket: listTicketRelativeSelect?.map(t => t.id),
    });
    if (dataResp) {
      const dataRespFilter = dataResp.filter((f: any) => f.value);

      // set value to form
      const formData = JSON.stringify({
        type: 'postFormEdit',
        data: dataRespFilter,
      });

      const scriptJSX = `window.postMessage('${formData
        .replace(/\\/g, '\\\\')
        .replace(/'/g, "\\'")}', '*');`;
      setTimeout(() => {
        webviewRef?.current?.injectJavaScript(scriptJSX);
      }, 300);
    }
  };

  const renderRightHeader = () => {
    return (
      <View style={styles.ctnRenderRightHeader}>
        <DropdownCM
          listDropdown={dataProrityDummy}
          onChangeItem={setDataPrority}
          dropDownStyle={{right: SizeDP(50)}}
          renderShowData={
            <View style={styles.renderShow}>
              {dataPrority.icon}
              <IconArrowGrey
                fill={'#fff'}
                width={SizeDP(12)}
                height={SizeDP(12)}
              />
            </View>
          }
        />
        <View>
          <DropdownCM
            listDropdown={dataTiket}
            onChangeItem={item => handelSelectInfo(item)}
            containerStyle={{width: SizeDP(12)}}
          />
        </View>
      </View>
    );
  };

  const renderPersonTicket = () => {
    return (
      <>
        {!isEmpty(dataAccount) && (
          <View style={styles.ctnTicketPerson}>
            <View style={styles.ctnUser}>
              <Image
                width={SizeDP(50)}
                height={SizeDP(50)}
                borderRadius={SizeDP(25)}
                source={{uri: dataAccount.imageUrl || String.DEFAULT_IMAGE}}
              />
              <TextCM>
                {'' + dataAccount.firstName + ' ' + dataAccount.lastName}
              </TextCM>
            </View>
            <View style={styles.ctnSub}>
              <IconMail width={SizeDP(24)} height={SizeDP(24)} />
              <TextCM>{'' + dataAccount.email}</TextCM>
            </View>
            <View style={styles.ctnSub}>
              <IconTree width={SizeDP(24)} height={SizeDP(24)} />
              <TextCM>{'' + dataAccount.acNameOrg}</TextCM>
            </View>
            <View style={styles.ctnSub}>
              <IconPhone width={SizeDP(24)} height={SizeDP(24)} />
              <TextCM>{'' + dataAccount.phone}</TextCM>
            </View>
          </View>
        )}
      </>
    );
  };

  const renderTicketRelative = () => {
    return (
      <>
        <HeaderCM
          style={{
            backgroundColor: '#fff',
          }}
          contentStyle={{
            backgroundColor: '#fff',
          }}
          titleStyle={{
            color: '#000810',
          }}
          fillIconBackLeft="#000810"
          title={t('HM.relatedTicket')}
          onPressIconLeft={() => setIsVisibleTicketRelative(false)}
          renderContentRight={() => (
            <TouchableOpacity onPress={handleAddTicket}>
              <TextCM style={styles.txtRight}>{t('CM.add')}</TextCM>
            </TouchableOpacity>
          )}
        />
        <Divider />
        {isEmpty(route?.params?.paramsSelectTicket) ? (
          <View style={styles.ctnArea}>
            <IconEmpty width={SizeDP(218)} height={SizeDP(149)} />
            <TextCM style={styles.textTitle}>
              {t('HM.relativeTicketEmpty')}
            </TextCM>
            <TextCM style={styles.textDes}>{t('HM.desRelativeTicket')}</TextCM>
          </View>
        ) : (
          <FlatList
            style={{paddingHorizontal: 16}}
            data={route?.params?.paramsSelectTicket}
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            renderItem={({item}: {item: ITicket}) => (
              <CardTicketRelative
                dataCard={item}
                account={dataAccount}
                select={route?.params?.paramsSelectTicket}
                onSelect={paramsSelectTicket =>
                  navigation.setParams({paramsSelectTicket})
                }
                isShowTrash={
                  !dataModeler?.inheritanceModeler?.modelerIds?.length
                }
              />
            )}
          />
        )}
      </>
    );
  };

  const getHeader = () => {
    if (actionTicketFrom !== 'eRequest') {
      return `${route?.params?.dataTicket?.flowName}`;
    }
    switch (action) {
      case 'create':
        return t('HM.addSearch');
      case 'multi':
        return t('HM.muiTicket');
      case 'edit':
        return t('HM.editTicket');
      default:
        return '';
    }
  };

  const onScannedImage = (image: string[]) => {
    if (!isEmpty(image)) {
      const data = JSON.stringify({
        type:
          image.length === 1
            ? 'native_post_scan_OCR'
            : 'native_post_scan_OCR_mutiples',
        dataFile: {
          name: image.length === 1 ? `img_scan_${Date.now()}.png` : '',
          mimeType: 'image/png',
          base64: image.length === 1 ? image[0] : image,
        },
      });

      const scriptJSX = `window.postMessage('${
        Platform.OS === 'ios'
          ? data
          : data.replace(/(\r\n|\n|\r|\s)/gm, '').replace(/\\/g, '\\\\')
      }', '*');`;
      webviewRef?.current?.injectJavaScript(scriptJSX);
    } else {
      Toast.show({
        type: 'error',
        text1: t('CM.noti'),
        text2: 'Không lấy được thông tin dữ liệu scan',
      });
    }
    setIsShowModalScan(false);
  };

  return (
    <>
      <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
        <>
          <StatusBar
            translucent
            barStyle={'dark-content'}
            backgroundColor={Color.Transparent}
          />
          <HeaderCM
            style={styles.ctnHeader}
            contentStyle={styles.ctnHeader}
            titleStyle={{
              color: Color.TextPrimary,
            }}
            alignment="start"
            title={getHeader()}
            fillIconBackLeft=""
            renderContentRight={renderRightHeader}
          />
          <Divider />
          <View
            style={[
              {flex: 1, backgroundColor: Color.BgBlur},
              Platform.OS === 'android' && {
                paddingBottom:
                  heightOfKeyboard > 0 ? heightOfKeyboard - 100 : 0,
              },
            ]}>
            {!isEmpty(formVersion) && (
              <WebView
                nestedScrollEnabled={true}
                showsHorizontalScrollIndicator={false}
                showsVerticalScrollIndicator={false}
                startInLoadingState
                injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
                allowsInlineMediaPlayback={true}
                mediaPlaybackRequiresUserAction={false}
                sharedCookiesEnabled={true}
                thirdPartyCookiesEnabled={true}
                domStorageEnabled={true}
                javaScriptEnabled={true}
                ref={webviewRef}
                onMessage={onMessage}
                source={{uri: getConfig().BASE_URL_FORM + formVersion}}
                webviewDebuggingEnabled
                onLoadEnd={() => setShowAction(true)}
              />
            )}
          </View>
          <Divider />
          {showAction && (
            <SafeAreaView
              edges={['bottom', 'left', 'right']}
              style={styles.ctnBtn}>
              <View style={styles.ctnFooter}>
                <View
                  style={[
                    styles.footerLeft,
                    actionTicketFrom !== 'eRequest' && {
                      flex: 0.4,
                    },
                  ]}>
                  <TouchableOpacity
                    onPress={() => {
                      setIsVisibleTicketRelative(true);
                      fieldDataTicketName('relative');
                    }}
                    style={styles.ctnItem}>
                    <Icon
                      fill={'rgba(0, 32, 77, .7)'}
                      name="swap-outline"
                      width={24}
                      height={24}
                    />
                    <TextCM style={styles.txtItem}>{t('HM.relative')}</TextCM>
                  </TouchableOpacity>
                  {actionTicketFrom === 'eRequest' && (
                    <>
                      <View style={styles.ctnDivider} />
                      <TouchableOpacity
                        onPress={() => handleConfirmData('draft', true)}
                        style={styles.ctnItem}>
                        <Icon
                          fill={'rgba(0, 32, 77, .7)'}
                          name="save-outline"
                          width={24}
                          height={24}
                        />
                        <TextCM style={styles.txtItem}>
                          {t('TK.save_draft')}
                        </TextCM>
                      </TouchableOpacity>
                    </>
                  )}
                </View>
                <TouchableOpacity
                  onPress={() => handleConfirmData('processing')}
                  style={[
                    styles.footerRight,
                    actionTicketFrom !== 'eRequest' && {
                      flex: 0.6,
                    },
                  ]}>
                  <TextCM
                    style={{
                      color: Color.White,
                    }}>
                    {t('HM.sendServices')}
                  </TextCM>
                </TouchableOpacity>
              </View>
            </SafeAreaView>
          )}

          <BottomSheetCM
            isVisible={isVisible}
            onClose={() => setIsVisible(false)}
            title={t('HM.infoPerson')}
            renderContent={renderPersonTicket()}
          />

          <ModalFullScreen
            isVisible={isVisibleTicketRelative}
            children={renderTicketRelative()}
          />

          {/* <ModalConfirmCM
            isVisible={isVisibleCancel}
            onClose={() => handleCancel('cancel')}
            title={t('HM.titleCancel')}
            content={t('HM.contentCancel')}
            type="cancel"
            labelCancel={t('HM.cancel')}
            labelConfirm={t('CM.labelCancel')}
            styleBtnRight={{backgroundColor: Color.RedE14337}}
            onConfirm={handleConfirmCancel}
          /> */}

          <ModalConfirmCM
            isVisible={isVisibleInfo}
            onClose={() => handleCancel('info')}
            title={t('HM.serviceName')}
            content={t('HM.contentFill')}
            type="info"
            labelCancel={t('CM.labelCancel')}
            labelConfirm={
              ticketStatusSelect.status === 'processing'
                ? t('HM.sendServices')
                : t('CM.save')
            }
            styleBtnRight={{backgroundColor: Color.BgPrimary}}
            onConfirm={handleConfirmInfo}
            renderContent={() => (
              <View style={{width: '100%', marginVertical: SizeDP(6)}}>
                <TextFieldCM
                  isDisabled={isDisableTicketName}
                  label={t('HM.enterName')}
                  value={ticketName}
                  onChangeText={setTicketName}
                  errorMessage={errorMessage}
                />
              </View>
            )}
          />

          <LoadingCM isVisible={isLoading} />
        </>
      </TouchableWithoutFeedback>

      <ModalFullScreen
        isVisible={showModalListModelerId}
        children={
          <ListServicesRelative
            onClose={() => setShowModalListModelerId(false)}
            modeler={dataModeler}
            setListTicketRelativeSelect={setListTicketRelativeSelect}
            listTicketRelativeSelect={listTicketRelativeSelect}
            handleSetParamsConfig={handleSetParamsConfig}
          />
        }
      />

      <ModalFullScreen
        isVisible={isShowModalScan}
        children={<ScanScreen onScannedImage={onScannedImage} />}
      />
    </>
  );
};

export default CreateTicketScreen;
